$primary: #222222 !default; $secondary: #ffffff !default; $tertiary: #0088cc !default; $quaternary: #e45735 !default; $header_background: #ffffff !default; $header_primary: #333333 !default; $highlight: #ffff4d !default; $danger: #e45735 !default; $success: #009900 !default; $love: #fa6c8d !default;  $public_image_path: "http://localhost:3000/images";  @import "common/foundation/variables"; @import "common/foundation/mixins"; div.poll {
  margin: 1em 0;
  border: 1px solid var(--primary-low);

  ul,
  ol {
    margin: 0;
    padding: 0;
    list-style: none;
    display: inline-block;
    width: 100%;
  }

  li {
    cursor: pointer;
    font-size: $font-up-1;
  }

  li[data-poll-option-id] {
    color: var(--primary);
    padding: 0.5em 0;
    word-break: break-word;
  }

  img {
    // Hacky way to stop images without width/height
    // from causing abrupt unintended scrolling
    &:not([width]):not(.emoji),
    &:not([height]):not(.emoji) {
      width: 200px !important;
      height: 200px !important;
      object-fit: contain;
    }
  }

  .poll-info {
    color: var(--primary-medium);
    vertical-align: middle;
    padding: 1em 0;

    .info-label {
      font-size: $font-up-4;
      line-height: $line-height-medium;
    }

    .info-text {
      margin: 0.25em 0;
      display: block;
    }
  }

  .poll-container {
    vertical-align: middle;
    padding: 0.5em 1em;

    .poll-results-number-rating {
      font-size: $font-up-5;
    }
  }

  .poll-buttons {
    .info-text {
      margin: 0.25em 0;
      color: var(--primary-medium);
    }
    .info-text + .info-text,
    button + .info-text {
      margin-left: 0.5em;
    }
  }

  .poll-voters:not(:empty) {
    min-height: 30px;
    margin-bottom: 0.25em;

    li {
      display: inline;
    }
  }

  .poll-voters-toggle-expand {
    width: 100%;
    text-align: center;

    .spinner {
      margin-top: 0.25em;
    }
  }

  .results {
    > li {
      cursor: default;
      padding: 0.25em 0;
      &:last-child {
        padding-bottom: 0;
      }
    }
    .option {
      p {
        margin: 0;
      }
    }

    .percentage {
      float: right;
      color: var(--primary-medium);
      margin-left: 0.25em;
    }

    .bar-back {
      background: var(--primary-low);
    }

    .bar {
      height: 0.75em;
      background: var(--primary-medium);
    }

    .chosen .bar {
      background: var(--tertiary);
    }
  }

  &[data-poll-type="number"] {
    li[data-poll-option-id] {
      display: inline-block;
      width: 3.25em;
      margin-right: 0.25em;
    }
  }

  .pie-chart-legends {
    justify-content: center;
    text-align: center;
    margin-top: 0.5em;

    .legend {
      align-items: center;
      cursor: pointer;
      display: inline-flex;
      flex-direction: row;
      margin-left: 1em;
      font-size: var(--font-down-0);

      .swatch {
        margin-right: 0.5em;
        display: inline-block;
        height: 16px;
        width: 16px;
      }
    }
  }

  .poll-grouped-pies-controls {
    display: flex;
    justify-content: space-between;
  }

  .poll-results-chart {
    overflow-y: auto;
    overflow-x: hidden;
  }

  .poll-show-breakdown {
    margin-bottom: 0.25em;
  }
}

div.poll.pie {
  .poll-container {
    display: inline-block;
  }
  .poll-info {
    display: inline-block;
    width: 150px;
  }
}

// hides 0 vote count in crawler and print view
body.crawler {
  .poll {
    .poll-info,
    .poll-button {
      display: none;
    }
  }
}
.poll-ui-builder-modal {
  .show-advanced {
    margin-left: auto;
    margin-right: 0;
  }
}

.poll-ui-builder {
  label {
    font-weight: bold;
  }

  .input-group {
    margin-bottom: 15px;

    input[type="number"],
    input[type="text"],
    input[type="time"],
    .combo-box,
    .multi-select,
    .select-kit-header,
    .date-picker-wrapper {
      width: 100%;
    }

    .date-picker-wrapper {
      margin-right: 5px;
    }
  }

  .poll-checkbox {
    label {
      width: unset;
    }
  }

  .radio-group {
    display: inline-block;
    margin-right: 30px;

    input {
      vertical-align: top;
    }

    label {
      display: inline-block;
    }
  }

  .poll-type {
    display: block;

    .poll-type-value {
      border: none;
      color: var(--primary);
      display: inline-block;
      padding: 6px 12px 3px;

      &.active {
        background-color: var(--quaternary);
        color: var(--secondary);
      }
    }
  }

  .poll-options {
    margin-bottom: 15px;

    .poll-option-value {
      align-items: center;
      display: flex;
      justify-content: space-between;
      margin-bottom: 3px;

      button {
        margin-left: 3px;
      }
    }

    .poll-option-controls {
      margin-top: 10px;
    }

    .tip {
      display: inline-block;
      vertical-align: bottom;
    }
  }

  .tip {
    display: block;
    font-size: $font-down-1;
    margin: -0.5em 0 0.5em;
  }

  .d-editor-preview {
    width: 100%;
  }
}
.poll-breakdown-modal {
  .modal-inner-container {
    max-width: unset;
    width: 90vw;
  }

  .modal-tabs {
    justify-content: flex-end;
  }

  .modal-body {
    display: grid;
    height: 80vh;
    grid: auto-flow / 1fr 2fr;
    padding: 0;
  }
}

.poll-breakdown-sidebar {
  background: var(--primary-very-low);
  box-sizing: border-box;
  padding: 1rem;
}

.poll-breakdown-title {
  font-weight: 600;
  margin: 0;
  text-align: center;
}

.poll-breakdown-total-votes {
  font-size: 1.2rem;
  font-weight: 600;
  margin-top: 0.5rem;
  text-align: center;
}

.poll-breakdown-options {
  display: grid;
  list-style: none;
  margin: 1.5rem 0 0 0;
}

.poll-breakdown-option {
  align-items: center;
  border-radius: 5px;
  column-gap: 0.66rem;
  cursor: default;
  display: grid;
  grid-template-columns: 2.5rem 1fr;
  row-gap: 0.1rem;
  justify-content: start;
  padding: 0.5rem 1rem;
}

.poll-breakdown-option-color {
  align-self: end;
  border: 1px solid rgba(0, 0, 0, 0.15);
  grid-column: 1;
  height: 0.6rem;
  justify-self: center;
  width: 1.2rem;
}

.poll-breakdown-option-count {
  align-self: start;
  font-size: 0.9rem;
  grid-column: 1;
  justify-self: center;
}

.poll-breakdown-option-text {
  grid-column: 2;
  grid-row: 1/3;
}

.poll-breakdown-body {
  box-sizing: border-box;
  padding: 1rem 2rem;
}

.poll-breakdown-body-header {
  align-items: center;
  border-bottom: 1px solid var(--primary-low);
  display: flex;
  flex: 0 0 auto;
  padding-bottom: 0.5rem;
}

.poll-breakdown-body-header-label {
  font-size: 1.2rem;
  font-weight: 600;
  margin: 0;
}

.poll-breakdown-dropdown {
  margin-left: 1rem;
}

.poll-breakdown-charts {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(33.3%, 0.33fr));
}

.poll-breakdown-chart-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-top: 1rem;
  position: relative;
}

.poll-breakdown-chart-label {
  display: block;
  text-align: center;
}
